// @flow
import * as React from 'react';
import {Chip, ChipProps} from "@mui/material";

type BlenderChipProps = {
  version: string;
} & ChipProps;
export const BlenderChip = ({version, sx, ...rest}: BlenderChipProps) => {
  return (
      <Chip label={version}
            avatar={<img src="data:image/png;base64," alt="Blender" style={{width: 24, height: 24}}/>}
            sx = {{
              backgroundColor: "#214C93",
              color: "white",
              ...sx}}
            {...rest}
      />
  );
};