import * as React from 'react';
import {useEffect} from 'react';
import {Stack} from "@mui/material";
import {Link as RouterLink, useSearchParams} from "react-router-dom";
import Chip from "@mui/material/Chip";
import {NodeTreeProfile} from "../../proto/application/nodetreestore/NodeTreeStoreMessage";
import {
  deleteNodeTree,
  getNodeTree,
  likeNodeTree,
  unlikeNodeTree,
  updateDescription,
  updateTags
} from "./service/NodeTreeStoreService";
import EditIcon from '@mui/icons-material/Edit';
import {Typography} from "../../component/wrapper/MuiWrappers";
import {ResourceType} from "../../proto/framework/ResourceMessage";
import CommentWidget from "../comment/CommentWidget";
import {getLikeCount, getUserLike} from "./NodeTreeStoreUtil";
import ProfileAvatar from "../customer/component/ProfileAvatar";
import {get_customer_url, get_node_tree_store_url} from "../../navigation/routerUtil";
import {timeForRender} from "../../api/util/TimeUtil";
import ImageContainer from "../../component/container/ImageContainer";
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import HelpIcon from '@mui/icons-material/Help';
import ThumbUpAltIcon from "@mui/icons-material/ThumbUpAlt";
import ThumbUpOffAltIcon from "@mui/icons-material/ThumbUpOffAlt";
import {toReadableNumber} from "../../api/util/StringUtil";
import AlertContext from "../../context/AlertContext";
import ListComponent from "../../component/container/ListComponent";
import {EngineChip} from "./component/EngineChip";
import {TypeChip} from "./component/TypeChip";
import {BlenderChip} from "./component/BlenderChip";
import ClearableTextField from "../../component/input/ClearableTextField";
import CopyButton from "../../component/button/CopyButton";
import {openDialog, openDialogWithPromise} from "../../component/dialog/DialogManager";
import {useIsUserOrAdmin, useSmartNavigate} from "../../hook/CommonUiHook";
import {NodeTreePreviewDefaultImg,} from "./component/NodeTreePreviewDefaultImg";
import OneOf from "../../component/container/OneOf";
import {useTranslation} from "react-i18next";
import ContextMenu from "../../component/menu/ContextMenu";
import ContentButton from "../../component/button/ContentButton";

type NodeTreeItemWidgetProps = {};
const NodeTreeItemWidget = (props: NodeTreeItemWidgetProps) => {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useSmartNavigate();
  const {t} = useTranslation();
  const {showAlert} = React.useContext(AlertContext);

  const [nodeTreeProfile, setNodeTreeProfole] = React.useState<NodeTreeProfile | undefined>(void 0);

  const [like, setLike] = React.useState<boolean>(false);
  const [likeCount, setLikeCount] = React.useState<number>(0);
  const [likeLoading, setLikeLoading] = React.useState<boolean>(false);

  const [editingTags, setEditingTags] = React.useState<boolean>(false);
  const [tagsInLine, setTagsInLine] = React.useState<string>("");
  const [editingDescription, setEditionDescription] = React.useState<boolean>(false);
  const [description, setDescription] = React.useState<string>("");
  const [refresh, setRefresh] = React.useState<boolean>(false);

  const canEdit = useIsUserOrAdmin(nodeTreeProfile?.owner?.id);

  const toTagsInLine = (tags: string[]) => {
    if (!tags) {
      return "";
    }
    return tags.map((tag) => {
      return "#" + tag;
    }).join(" ");
  }

  const toTagsInArray = (tagsInLine: string) => {
    return tagsInLine.split(" #").map((tag) => {
      return tag.trim().replace("#", "");
    }).filter((tag) => {
      return tag.length > 0;
    });
  }

  const getCopyContent = () => {
    return JSON.stringify({
      "node_tree_id": nodeTreeProfile?.id,
      "node_tree_type": nodeTreeProfile?.type
    });
  }


  useEffect(() => {
    const id = searchParams.get("id");
    if (!id) {
      return;
    }
    getNodeTree(id).then((profile) => {
      setNodeTreeProfole(profile);
      setLike(getUserLike(profile));
      setLikeCount(getLikeCount(profile));
      setTagsInLine(toTagsInLine(profile.tags))
      setDescription(profile.description);

    }).catch((e) => {
      showAlert(e.message, "error");
    });

  }, [searchParams, refresh]);

  if (!nodeTreeProfile) {
    return <Stack>

    </Stack>
  }

  return <Stack spacing={1}>
    <Stack direction={{
      xs: "column",
      md: "row"
    }} spacing={1}
           justifyContent={
      "space-around"
    }>
      <Typography variant={"h4"}>{nodeTreeProfile.name}</Typography>
      <Stack direction={"row"} justifyContent={"center"} alignItems={"center"}  flexWrap={"wrap"} rowGap={1} spacing={1}>
        <RouterLink to={get_node_tree_store_url("", "", "", nodeTreeProfile.engine)}> <EngineChip
            engine={nodeTreeProfile.engine}/></RouterLink>
        <RouterLink to={get_node_tree_store_url("", "", nodeTreeProfile.type, "")}><TypeChip
            type={nodeTreeProfile.type}/></RouterLink>
        <BlenderChip version={nodeTreeProfile.blenderVersion}/>
        <ContextMenu menuItems={[
          <ContentButton title={"View Profile"} onClick={() => {
            navigate(get_customer_url(nodeTreeProfile.owner?.id))
          }}></ContentButton>,
          <ContentButton title={"Search Owner's Work"} onClick={() => {
            navigate(get_node_tree_store_url("", nodeTreeProfile.owner?.id, "", ""))
          }}></ContentButton>,
        ]}>
        <ProfileAvatar avatarUrl={nodeTreeProfile.owner?.avatarUrl}
                       title={nodeTreeProfile.owner?.name}
                       sx={{width: 32, height: 32}}
                       profileLink={get_node_tree_store_url("", nodeTreeProfile.owner?.id, "", "")}
        />
        </ContextMenu>
        <Chip
            label={timeForRender(nodeTreeProfile.updateAt)}
            variant={"outlined"}
        />
        {canEdit && <Chip
            label={<DeleteForeverIcon fontSize={"small"}/>}
            title={"Delete"}
            sx={{backgroundColor: "red", color: "white", borderRadius: 0}}
            onClick={() => {
              deleteNodeTree(nodeTreeProfile.id).then(() => {
                navigate(get_node_tree_store_url())
              });
            }}
            variant={"filled"}
        />}
      </Stack>
    </Stack>
    {editingTags ? <ClearableTextField
            value={tagsInLine}
            clearAction={() => {
              setEditingTags(false);
              setTagsInLine(toTagsInLine(nodeTreeProfile.tags));
            }}
            placeholder={"#tag1 #tag2"}
            setValue={setTagsInLine} onSubmit={() => {
          updateTags(nodeTreeProfile.id, toTagsInArray(tagsInLine)).then(() => {
            setRefresh(!refresh);
          }).catch((e) => {
            showAlert(e.message, "error");
          });
          setEditingTags(false);
        }}/> :
        <Stack direction={"row"} alignItems={"center"} justifyContent={"center"} flexWrap={"wrap"} rowGap={1}>
          <ListComponent data={[...nodeTreeProfile.tags]}
                         renderItem={(tag, index) => {
                           return <RouterLink key={index + tag}
                                              to={get_node_tree_store_url(tag, "", "", "")}>
                             <Chip label={tag}
                                   sx={{margin: 0.5}}
                                   variant={"outlined"}/></RouterLink>
                               ;
                         }}/>
          {canEdit && <Chip
              label={<EditIcon fontSize={"small"}/>}
              onClick={() => {
                setEditingTags(true);
              }}
              variant={"outlined"}
          />}
        </Stack>
    }
    <Stack direction={"row"} justifyContent={"center"} alignItems={"center"} spacing={1}>
      <OneOf index={nodeTreeProfile.avatarUrl === void 0 ? 0 : 1}>
        <NodeTreePreviewDefaultImg type={nodeTreeProfile.type} height={"70dvh"}/>
        <ImageContainer src={nodeTreeProfile.avatarUrl as string}
                        sx={{width: "100%", maxHeight: "70dvh", objectFit: "contain"}}
                        skeletonSx={{height: "70dvh"}}/>
      </OneOf>
      {canEdit && <Chip
          label={<EditIcon fontSize={"small"}/>}
          onClick={async () => {
            const result = await openDialogWithPromise("EditNodeTreeAvatarDialog", {
              nodeTreeId: nodeTreeProfile.id,
              avatarUrl: nodeTreeProfile.avatarUrl,
              type: nodeTreeProfile?.type
            });
            if (result.success) {
              // refresh
              setRefresh(!refresh);
            }
          }}
          variant={"outlined"}
      />}
    </Stack>
    {editingDescription ?
        <ClearableTextField value={description}
                            placeholder={"Description about the node tree"}
                            setValue={setDescription}
                            onSubmit={() => {
                              updateDescription(nodeTreeProfile.id, description).then(() => {
                                setRefresh(!refresh);
                              }).catch((e) => {
                                showAlert(e.message, "error");
                              });
                              setEditionDescription(false);
                            }}
                            clearAction={() => {
                              setEditionDescription(false);
                              setDescription(nodeTreeProfile.description);
                            }}
        />
        :
        <Stack direction={"row"} alignItems={"center"} justifyContent={"start"}
        ><Typography enableLinkify={true} variant={"body1"} flexGrow={1}
                     textAlign={"start"}>{ nodeTreeProfile.description.length === 0 ? "No Description about this NodeTree" :  nodeTreeProfile.description}</Typography>
          {canEdit && <Chip
              label={<EditIcon fontSize={"small"}/>}
              title={"Edit Description"}
              onClick={() => {
                setEditionDescription(true);
              }}
              variant={"outlined"}
          />}
        </Stack>
    }
    <Stack direction={"row"} alignItems={"center"} spacing={1}>
      <Chip
          onClick={
            () => {
              if (likeLoading) {
                return;
              }
              setLikeLoading(true);
              if (!like) {
                likeNodeTree(nodeTreeProfile.id).then(() => {
                  setLike(true);
                  setLikeCount(likeCount + 1);
                }).catch((e) => {
                  showAlert(e.message, "error");
                }).finally(() => {
                  setLikeLoading(false);
                });
              } else {
                unlikeNodeTree(nodeTreeProfile.id).then(() => {
                  setLike(false);
                  setLikeCount(likeCount - 1);
                }).catch((e) => {
                  showAlert(e.message, "error");
                }).finally(() => {
                  setLikeLoading(false);
                });
              }
            }
          }
          variant={"filled"}
          color={like ? "primary" : "default"}
          sx={{borderRadius: 0}}
          icon={like ? <ThumbUpAltIcon/> :
              <ThumbUpOffAltIcon/>}
          label={toReadableNumber(likeCount)}
      />
      <ClearableTextField value={getCopyContent()} onSubmit={() => void 0}
                          setValue={(value) => void 0}
                          sx={{flexGrow: 1}}
                          disabled
                          size={"small"}
                          variant={"standard"}
                          endAdornment={<CopyButton text={getCopyContent()}/>}/>
      <Chip
          onClick={() => {
            openDialog("NodeTreeHelperDialog")
          }}
          variant={"filled"}
          color={"info"}
          icon={<HelpIcon/>}
          sx={{borderRadius: 0}}
          label={t("How to use")}
      />
    </Stack>


    <CommentWidget objectId={nodeTreeProfile.id}
                   targetType={ResourceType.RESOURCE_TYPE_NODE_TREE}
                   canDelete={canEdit}
                   paddingTop={8}
    />
  </Stack>
};

export default NodeTreeItemWidget;