import {IconButton, IconButtonProps} from "@mui/material";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import copy from 'copy-to-clipboard';
import React from "react";
// CopyButton 是一个便捷实用按钮组件，它可以复制文本到剪贴板。
type CopyButtonProps = {
  text: string | (() => string);
  icon?: React.ReactNode;
} & IconButtonProps;

// https://stackoverflow.com/questions/57868405/safari-clipboard-error-typeerror-undefined-is-not-an-object
const CopyButton = ({text, icon = <ContentCopyIcon/>, ...rest}: CopyButtonProps) => {
  return <IconButton onClick={() => {
    return copy(text instanceof Function ? text() : text);
  }} {...rest}>{icon}</IconButton>
}

export default React.memo(CopyButton);