// @flow
import * as React from 'react';
import {Chip, ChipProps} from "@mui/material";
import {getEngineColor, getEngineLabel} from "../NodeTreeStoreUtil";

type EngineChipProps = {
  engine: string;
} & ChipProps;
export const EngineChip = ({engine, sx, ...rest}: EngineChipProps) => {
  return (
      <Chip label={getEngineLabel(engine)}
            sx = {{
              backgroundColor: getEngineColor(engine),
              color: "white",
              ...sx}}
            {...rest}
      />
  );
};