// @flow
import * as React from 'react';
import {Chip, ChipProps} from "@mui/material";
import {getTypeColor, typeToLabel} from "../NodeTreeStoreUtil";
import {useTranslation} from "react-i18next";


type TypeChipProps = {
  type: string;
} & ChipProps;
export const TypeChip = ({type, sx, ...rest}: TypeChipProps) => {
  const {t} = useTranslation();
  return (
      <Chip label={t(typeToLabel(type))}
            sx = {{
              backgroundColor: getTypeColor(type),
              color: "white",
              ...sx}}
            {...rest}
      />
  );
};